.video-container {
   display: flex;
   gap: 10px;
   align-self: center;
   justify-content: center;
   align-items: center;
   position: relative;

   @media (max-width: 800px) {
      flex-wrap: wrap;
      margin-top: 125px;
   }

   @media (max-width: 500px) {
      flex-wrap: wrap;
      margin-top: 75px;
   }

   .me,
   .user {
      display: flex;
      flex-direction: column;
      align-items: center;

      p {
         font-size: 18px;
         // font-family: Livvic-Bold;
         margin-top: 10px;
         color: white;
         background-color: #121212;
         padding-left: 10px;
         padding-right: 10px;
         line-height: 30px;   
      border-radius: 8px;   }
   }

   video {
      width: 100%;
      height: auto;
      border: 5px solid #121212;
      border-radius: 8px;
      margin-top: 3px;
      display: flex;

      @media (max-width: 800px) {
         height: 350px;
      }
      @media (max-width: 500px) {
         height: 200px;
      }
   }

   .hidden {
      display: none;
   }
}