.video-chat {
   padding: 10px;
   text-align: center;
   justify-content: center;
   display: flex;
   flex-direction: column;
   height: 100vh;
   background-image: url(https://images.unsplash.com/photo-1485846234645-a62644f84728?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1159&q=80);
   background-repeat: no-repeat;
   background-position: 50% 0;
   background-size: cover;
   align-items: center;
   position: relative;

   h1 {
      position: absolute;
      font-size: 45px;
      margin-top: 10px;
      width: 100%;
      top: 0;
      text-align: center;
   }

   .connects {
      position: absolute;
      width: 100%;
      top: 0;
      text-align: left;
      margin-top: 75px;
      padding: 10px;

      @media (max-width: 500px) {
         margin-top: 48px;
      }

      .title {
         font-family: Livvic-Bold;
         margin-bottom: 10px;
         @media (max-width: 500px) {
            margin-bottom: 0px;
         }
      }

      span {
         font-size: 13px;
      }

      p {
         text-align: left;
         margin-bottom: 5px;
      }
   }

   input {
      border: unset;
      border-radius: 5px;
      height: 35px;
      width: 300px;
      padding: 10px;
      font-family: Livvic-Regular;
   }

   button {
      border: unset;
      border-radius: 5px;
      height: 35px;
      padding: 10px;
      font-family: Livvic-Regular;
      margin-left: 10px;
      margin-bottom: 10px;

      background-color: #fff;
      &:hover {
         box-shadow: 0 0 4px 0px;
      }
   }
}